export function getdepartmentTree(id, key, type, enabled) {
    return new Promise((resolve, reject) => {
        http({
                url: API.departmentTree_API,
                method: 'POST',
                data: {
                    id: id,
                    key: key,
                    type: type,
                    enabled: enabled
                }
            })
            .then(({ data, status }) => {
                if (status != 200) {
                    //
                    return;
                }
                resolve(data);

            })
            .catch(error => {
                //
                console.log(error);
            })
    })
}
